* {
  padding: 0;
  margin: 0;
  box-sizing:
    border-box;
}


.carousel-item active img {
  width: 100%;
  height: 80vh;
}

.img {
  width: 100%;
  height: 80vh;
}


.Home {
  height: 630px;
  width: 1200px;
  display: block;
  margin-left: auto;
  margin-right: auto;
}

h1,
h4 {
  font-family: Georgia, 'Times New Roman', Times, serif;

}

.navbar-nav .nav-item:nth-child(n+2) {
  margin-left: 10px;
  /* Adjust this value to increase/decrease space */
}

@media screen and (max-width: 600px) {
  #Home {
    max-width: 100%;
    height: auto;
  }
}

@media screen and (max-width: 300px) {
  #Home {
    max-width: 100%;
    height: auto;
  }
}

body {
  text-align: justify;
}

.hero {
  position: relative;
  background: url("../public/ses.png") no-repeat center center/cover;
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 0 15px;
  /* Add space on both sides */
  text-align: center;
  color: white;
}

.hero::after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0,
      0,
      0,
      0.5);
  /* Add a dark overlay for better text visibility */
  z-index: 1;
}

.hero-content {
  position: relative;
  z-index: 2;
  /* Bring content above the overlay */
}

.hero h1 {
  font-size: 3rem;
  font-weight: bold;
  margin-bottom: 20px;
}

.hero p {
  font-size: 1.5rem;
  margin-bottom: 20px;
}

.hero a {
  font-size: 1.2rem;
  padding: 10px 20px;
  background: #0d6efd;
  color: white;
  text-decoration: none;
  border-radius: 5px;
}

.hero a:hover {
  background: #004085;
}

.nav-item .nav-link {
  text-decoration: none;
  position: relative;
  padding-bottom: 3px;
  display: inline-block;
  /* Ensures the link is only as wide as its text */
}

/* Apply underline only to non-dropdown links */
.nav-item .nav-link:not(.dropdown-toggle)::after {
  content: "";
  position: absolute;
  left: 50%;
  bottom: 0;
  width: 0;
  height: 1.5px;
  /* Bolder underline */
  background-color: currentColor;
  transition: width 0.2s ease-in-out, left 0.2s ease-in-out;
  border-radius: 2px;
  /* Smooth edges */
}

/* Ensure proper behavior on hover */
.nav-item .nav-link:not(.dropdown-toggle):hover::after {
  width: 100%;
  left: 0;
}

/* Fix issue on small screens */
@media (max-width: 991px) {
  .nav-item .nav-link {
    width: auto;
    /* Ensures it only takes the necessary width */
    text-align: center;
    /* Keeps text aligned properly */
  }
}

/* Exclude SES SOLUTION */
.navbar-brand {
  text-decoration: none !important;
}

.navbar-nav .nav-item .nav-link {
  font-weight: normal;
}